container, .portifa {
  background-color: rgb(0, 0, 0);
  color: white;
  min-height: 100vh;
}

.portifa {
  display: flex;
  flex-direction: columns;
  flex-wrap: wrap;
  justify-content: center;
  padding: 5vh 10vh;
  font-size: 1rem;
}
.portifa .Box {
  padding: 10%;
  justify-content: center;
  max-width: 100vw;
  flex-wrap: wrap;
  display: flex;
  justify-content: space-around;
  display: flex;
}
@media (max-width: 768px) {
  .portifa .Box {
    display: flex;
    padding: 0px;
    flex-direction: columns;
    flex-wrap: wrap;
    margin-bottom: 10%;
  }
  .portifa .Box h2 {
    font-size: 1rem;
  }
}
.portifa .Box .Project {
  display: flex;
  width: 25vw;
  flex-wrap: wrap;
  margin: 2%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1px solid rgb(58, 130, 255);
  padding: 2% 3%;
}
@media (max-width: 768px) {
  .portifa .Box .Project {
    margin: 0%;
    flex-wrap: nowrap;
    padding: 0px;
    font-size: 1.4rem;
    width: 100%;
    margin: 10vh;
    margin-bottom: 10%;
  }
  .portifa .Box .Project p, .portifa .Box .Project h2, .portifa .Box .Project h1 {
    font-size: 1.4rem;
  }
}
.portifa .Box .Project .descricao {
  font-size: 1rem;
  height: 20vh;
}
@media (max-width: 768px) {
  .portifa .Box .Project .descricao {
    width: 70%;
    font-size: 0.8rem;
    height: 80%;
    margin-bottom: 10%;
  }
}
.portifa .Box .Project .Links {
  display: flex;
  width: 10vh;
  height: 10vh;
  flex-direction: column;
}
@media (max-width: 768px) {
  .portifa .Box .Project .Links {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10%;
  }
}
.portifa .Box .Project .Links a {
  color: white;
}
.portifa .Box .Project .Links div {
  text-align: center;
  font-size: 1rem;
}
@media (max-width: 768px) {
  .portifa .Box .Project .Links div {
    display: flex;
    width: 40vw;
    margin-bottom: 10%;
  }
}
.portifa .Box .Project .React {
  color: rgb(0, 183, 255);
}
.portifa .Box .Project .Javascript {
  color: yellow;
}/*# sourceMappingURL=portifolio_modules.css.map */