@import './../../style_Variaveis/Variaveis.scss';
.portifa{
  @extend container;
  display: flex;
  flex-direction: columns;
  flex-wrap: wrap;
  justify-content: center;
  padding: 5vh 10vh;
  font-size: 1rem;
 
 
  .Box{
    padding: 10%;
    justify-content: center;
    max-width: 100vw;
    flex-wrap: wrap;
    display: flex;
    justify-content: space-around;
    display: flex;
    @include mobile{
      display: flex;
      padding: 0px;
      flex-direction: columns;
      flex-wrap: wrap;
      h2{
        font-size: 1rem;
      }
     }
    .Project{
    display: flex;
    width: 25vw;
    flex-wrap: wrap;
    margin: 2%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px solid $primary-color;
    padding: 2% 3%;
    @include mobile{
      margin: 0%;
      flex-wrap: nowrap;;
      padding: 0px;
     font-size: 1.4rem;
     width: 100%;
      margin: 10vh;
      p,h2,h1{
        font-size: 1.4rem;
      }
  
     
     .Linguagem{
    
      
      
     
     
     }
     
     }
     .descricao{
      font-size: 1rem;
      height: 20vh;
      @include mobile{
        width: 70%;
        font-size:0.8rem;
        height: 80%;
      }
     }
    
    .Links{
      display: flex;
      width: 10vh;
      height: 10vh;
      flex-direction: column;
      @include mobile{
        display: flex;
        justify-content: center;
        align-items: center;;
        
      }
      a{
        color: white;
      }
      div{
        text-align: center;
       font-size: 1rem;
       @include mobile{
       display: flex;
       width: 40vw;
       }
      }
    }
     .React{
       color: rgb(0, 183, 255);
     }
     .Javascript{
       color: yellow;
     }
   }}
  
};
